import React, { Component } from 'react';
import { Badge, Button, Dropdown } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xbadge extends Component {

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	render() {
		const { optionsjs} = this.props.configs

		return (
			<div id="x-badge">
				<h3>Badge 标记</h3>
				<div className="mt-20 mb-20">
					<Badge value={12}>
						<Button size="small">评论</Button>
					</Badge>
					<Badge value={3}>
						<Button size="small">回复</Button>
					</Badge>
					<Dropdown trigger="click" menu={(
						<Dropdown.Menu>
							<Dropdown.Item className="clearfix">
								<span>评论</span><Badge className="mark" value={12} />
							</Dropdown.Item>
							<Dropdown.Item className="clearfix">
								<span>回复</span><Badge className="mark" value={3} />
							</Dropdown.Item>
						</Dropdown.Menu>
					)}
					>
						<span className="el-dropdown-link">
							点我查看<i className="el-icon-caret-bottom el-icon--right"></i>
						</span>
					</Dropdown>
				</div>
				<Codemirror value={
					`<Badge value={12}>
	<Button size="small">评论</Button>
</Badge>
<Badge value={3}>
	<Button size="small">回复</Button>
</Badge>
<Dropdown trigger="click" menu={(
	<Dropdown.Menu>
		<Dropdown.Item className="clearfix">
			<span>评论</span><Badge className="mark" value={12} />
		</Dropdown.Item>
		<Dropdown.Item className="clearfix">
			<span>回复</span><Badge className="mark" value={3} />
		</Dropdown.Item>
	</Dropdown.Menu>
)}
>
	<span className="el-dropdown-link">
		点我查看<i className="el-icon-caret-bottom el-icon--right"></i>
	</span>
</Dropdown>`
				} options={optionsjs} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xbadge)
